.button {
  color: @color-text-white;
  background-color: @color-basic-green;
  width: 100%;
  min-height: 40px;
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  padding: 0;
  font-weight: 400;
  border: none;

  &:hover {
    background-color: @color-hover-green;
  }

  &:active {
    color: fade(@color-text-white, 30%);
  }
}

.button--promo {
  font-size: 14px;
  line-height: 18px;
  padding: 11px 0;

  @media (min-width: @tablet-width) {
    width: 244px;
    height: 54px;
    font-size: 20px;
    line-height: 20px;
    padding: 17px 0;
    z-index: 3;
  }
}

.button--form-submit {
  font-family: 'Oswald';
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-style: normal;
  margin-bottom: 21px;
  cursor: pointer;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    width: 282px;
    height: 52px;
    margin-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    width: 568px;
  }

}

.button--catalog {
  font-size: 16px;
  line-height: 20px;
  padding: 10px 0;

  @media (max-width: @tablet-width) {
    grid-column-start: 1;
    grid-column-end: 3;
  }

  @media (min-width: @tablet-width) {
    margin-top: auto;
  }
}

.button--show-goods {
  background-color: @color-fill-grey;
  color: @color-text-special-grey;
  padding: 8px 0;

  &:hover {
    background-color: @color-hover-grey;
  }

  &:active {
    color: fade(@color-text-special-grey, 30%);
  }

  @media (min-width: @tablet-width) {
    margin-top: auto;
  }
}

