.main-nav {
  position: relative;
}
.main-nav__menu-toggle {
  position: absolute;
  right: 20px;
  top: -50px;
  width: 18px;
  height: 18px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-nav__menu-toggle {
    display: none;
  }
}
.main-nav--nojs .main-nav__menu-toggle {
  display: none;
}
.main-nav--opened .main-nav__menu-toggle::before,
.main-nav--opened .main-nav__menu-toggle::after {
  content: "";
  position: absolute;
  left: -3px;
  width: 25px;
  height: 1px;
  background-color: black;
}
.main-nav--opened .main-nav__menu-toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__menu-toggle::after {
  transform: rotate(-45deg);
}
.main-nav--closed .main-nav__menu-toggle {
  width: 24px;
  height: 16px;
  top: -48px;
}
.main-nav--closed .main-nav__menu-toggle::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 24px;
  height: 1px;
  background-color: black;
  box-shadow: 0 7px 0 0 black,
    0 15px 0 0 black;
}
